export const getUserData = async (userID, accessToken) => {
    try {
        const response = await axios.get(
            `https://www.strava.com/api/v3/athletes/${userID}/activities?access_token=${accessToken}`
        );
        return response;
    } catch (error) {
        console.log(error);
    }
};