import { createSlice } from '@reduxjs/toolkit'
import { getUserData } from '../utils/functions'
import moment from 'moment'


const stravaActivitiesReducer = createSlice({
	name: 'activities',
	initialState: {},
	reducers: {
		setActivities(state, action) {
			return action.payload
		},
		removeActivities() {
			return []
		},

	}
})

export const { setActivities, removeActivities } = stravaActivitiesReducer.actions

export const initializeActivities = (userId, accessToken) => {
    return async (dispatch) => {
        console.log(userId, accessToken)
        const activities = await getUserData(userId, accessToken)
        const monthNow = moment().format("MMM")
        const filteredActivities = activities.data.filter((d) => moment(d.start_date).format("MMM") === monthNow)
        console.log(monthNow, filteredActivities)
		dispatch(setActivities(filteredActivities))
    }
}

export const emptyConversations = () => {
	return async (dispatch) => (
		dispatch(removeActivities())
	)
}

export default stravaActivitiesReducer.reducer