import { createSlice } from '@reduxjs/toolkit'
import { testAuthGetter, testAuthGetter2 } from '../utils/functions'
import userService from '../services/users'

const stravaUserReducer = createSlice({
	name: 'stravauser',
	initialState: {},
	reducers: {
		setStravaUser(state, action) {
			return action.payload
		},
		removeStravaUser() {
			return []
		},
	}
})

export const { setStravaUser, removeStravaUser } = stravaUserReducer.actions

export const initializeUser = (data, usertype) => {
    return async (dispatch) => {
		console.log(data)
		
		if (usertype === 'newuser') {
			const user = await testAuthGetter(data.refresh_token)
			console.log({...data, access_token: user.access_token})
			
			dispatch(setStravaUser({...data, access_token: user.access_token}))
		} 
		if (usertype === "olduser") {
			const user = await testAuthGetter2(data.refresh_token)
			console.log(user)
			console.log(data)
			dispatch(setStravaUser({...data, access_token: user.access_token}))
		}
		
	}
}

export const emptyConversations = () => {
	return async (dispatch) => (
		dispatch(removeStravaUser())
	)
}

export default stravaUserReducer.reducer