import React, { useRef, useState } from "react";
import loginService from '../services/login'
import userService from '../services/users'
import { AlertDialog, AlertDialogBody,  FormControl,
    FormLabel,
 AlertDialogContent, AlertDialogHeader, AlertDialogOverlay, Button, useDisclosure, Input, InputGroup, InputRightElement, AlertDialogFooter } from "@chakra-ui/react";
import OAuth2Login from "react-simple-oauth2-login";
import { useDispatch } from "react-redux";
import { initializeUser } from "../reducers/stravaUserReducer";
import { testAuthGetter } from "../utils/functions";

const REACT_APP_CLIENT_ID  = process.env.CLIENT_ID



const Home = () => {
    const dispatch = useDispatch()
    const [succesfulStrava, setSuccesfulStrava] = useState(false)
  const [stravaId, setStravaId] = useState('')
      const onFailure = response => console.error("EI ONNANN", response);
    const { isOpen, onOpen, onClose } = useDisclosure()
    const cancelRef = useRef()
    const [show, setShow] = useState(false)
    const [stravaconnect, setStravaConnect] = useState(false)
    const [username, setUsername] = useState('')
    const[name, setName] = useState('')
    const [refresh_token, setRefreshCode] = useState('')
    
    const [password, setPassword] = useState('')


    const onSuccess = async (response) => {
        console.log("Onnas")
        setStravaConnect(false)
        const getStravaCodes = await testAuthGetter(response.code)
        console.log(getStravaCodes)
        setUsername(getStravaCodes.athlete.username)
        setRefreshCode(getStravaCodes.refresh_token)
        setStravaId(getStravaCodes.athlete.id)
        setName(getStravaCodes.athlete.firstname.concat(' ').concat(getStravaCodes.athlete.lastname))

        setSuccesfulStrava(true)
        // dispatch(initializeUser(response.code))
       
      }

    const handleClick = () => setShow(!show)

    const handleLogin = async () => {
        console.log("handle")
        try {
             const user = await loginService.login({username, password})
             let usertype = "olduser"
            dispatch(initializeUser(user, usertype))
            setStravaConnect(false)
        } catch(error) {
            console.log(error)
            setSuccesfulStrava(true)
            setStravaConnect(true)
        }
    };
    const handleNewAccount = async () => {
        console.log("NEW")
        try {
            const userinfo = {
                name: name,
                username: username,
                refresh_token: refresh_token,
                stravaid: stravaId,
                password: password,
            }
            console.log(userinfo)
            const newAccount = await userService.addUser(userinfo)
            if (newAccount) {
                const user = await loginService.login({username: username, password: password})
                console.log(user)
                let usertype = "olduser"
                dispatch(initializeUser(user, usertype))
                setSuccesfulStrava(false)
                setStravaConnect(false)
            }
        } catch(error) {
            console.log(error)
        }
    }

    return (
        <div>
            <h1>Home</h1>
            <Button onClick={onOpen} colorScheme='gray' style={{margin: 10}}>LOG IN </Button>
            <AlertDialog   motionPreset='slideInBottom'
            leastDestructiveRef={cancelRef}
            onClose={onClose}
            isOpen={isOpen}
            isCentered>
            <AlertDialogOverlay>
                <AlertDialogContent>
                    <AlertDialogHeader>
                        Log in
                    </AlertDialogHeader>
                    <AlertDialogBody>
                        {stravaconnect === true ? <OAuth2Login authorizationUrl="http://www.strava.com/oauth/authorize"
     responseType="code"
     clientId="117995"
     redirectUri={process.env.REDIRECTURI}
     scope="read_all,activity:read_all"
     extraParams={{ approval_prompt: 'force' }}
     onSuccess={onSuccess}
     onFailure={onFailure} 
     isCrossOrigin={true}/> :<FormControl>
     <FormLabel>
         Username
     </FormLabel>
     <Input
         pr='4.5rem'
         placeholder='Enter username'
         value={username}
         onChange={e => setUsername(e.target.value)}
     />
     {
        succesfulStrava === true ? 
        <><FormLabel>
                                                Name
                                            </FormLabel><Input
                                                    pr='4.5rem'
                                                    placeholder='Enter username'
                                                    value={name}
                                                    onChange={e => setName(e.target.value)} /></> : null
     }
        <FormLabel>
         Password
     </FormLabel>
     <InputGroup size='md'>
    
     <Input
         pr='4.5rem'
         type={show ? 'text' : 'password'}
         placeholder='Enter password'
         value={password}
         onChange={e => setPassword(e.target.value)}
     />
     <InputRightElement width='4.5rem'>
         <Button h='1.75rem' size='sm' onClick={handleClick}>
         {show ? 'Hide' : 'Show'}
         </Button>
     </InputRightElement>
     </InputGroup>
 </FormControl> }
                    
                    </AlertDialogBody>
                    <AlertDialogFooter>
                    <Button ref={cancelRef} onClick={onClose}>
                        Peruuta
                    </Button>
                    {succesfulStrava === true ?  <Button onClick={handleNewAccount}>
                        Tallenna uusi käyttäjä ja kirjaudu
                    </Button> : <Button onClick={handleLogin}>
                        Kirjaudu sisään
                    </Button>}
                    
                </AlertDialogFooter>
                </AlertDialogContent>
                
            </AlertDialogOverlay>
            </AlertDialog>
            
        </div>
    );
};

export default Home;