import {
    Button,
    AlertDialog,
    AlertDialogOverlay,
    AlertDialogContent,
    AlertDialogHeader,
    useDisclosure,
    AlertDialogBody,
    AlertDialogFooter,
    TableContainer,
    Table,
    TableCaption,
    Thead,
    Tr,
    Th,
    Tbody,
  } from '@chakra-ui/react'
  import moment from 'moment'
import { useRef } from 'react'


const Statistics = ({activities, athlete}) => {

    console.log(athlete) 
    const { isOpen, onOpen, onClose } = useDisclosure()
    const cancelRef = useRef()
    let total = {km: 0, time: 0}
    const statistics = activities.reduce((acc, curr) => {
        let item = acc.find(item => item.type === curr.type)
        if (item) {
          item.km += curr.distance / 1000
          total.km += curr.distance / 1000
          item.heartrate = curr.average_heartrate !== undefined ? [...item.heartrate, curr.average_heartrate] :  [...item.heartrate]
          item.time += curr.elapsed_time
          total.time += curr.elapsed_time
          item.speed = [...item.speed, (curr.average_speed * 3.6)]
        } else {
          acc.push({
            type: curr.type,
            km: curr.distance / 1000,
            heartrate: curr.average_heartrate !== undefined ? [curr.average_heartrate] : [],
            time: curr.elapsed_time, 
            speed: [(curr.average_speed * 3.6)]
          })
          total.km += curr.distance / 1000
          total.time += curr.elapsed_time
         
        }
        return acc 
      },[])

      console.log(statistics, total)
     

    return (
        <><Button onClick={onOpen} colorScheme='gray' style={{margin: 10}}>Kuukauden statistiikka </Button><AlertDialog motionPreset='slideInBottom'
            leastDestructiveRef={cancelRef}
            onClose={onClose}
            isOpen={isOpen}
            isCentered
        >
            <AlertDialogOverlay />
            <AlertDialogContent minWidth='fit-content'>
                <AlertDialogHeader> Kuukauden statistiikka </AlertDialogHeader>

                <AlertDialogBody>
                    <TableContainer width="100%">
                        <Table variant='striped' colorScheme='teal'>
                            <TableCaption>{athlete.name}, {moment().format("MMMM YYYY")} (Strava)</TableCaption>
                            <Thead>
                                <Tr>
                                    <Th>Sport</Th>
                                    <Th>Kilometers</Th>
                                    <Th>Avg Hr</Th>
                                    <Th>time</Th>
                                    <Th>avg speed</Th>
                                </Tr>
                            </Thead>

                            <Tbody>
                                {statistics?.map((s) => (
                                    
                                    <Tr>
                                        <Th>{s.type}</Th>
                                        <Th>{Math.floor(s.km * 100) / 100}</Th>
                                        <Th>{s.heartrate[0] !== undefined ? Math.floor((s.heartrate.reduce((a, b) => a + b, 0) / s.heartrate.length) * 100) / 100 : " Ei tietoja"}</Th>
                                        <Th> {moment.duration(s.time, 'seconds').format("HH:mm:ss", { trim: false })}</Th>
                                        <Th> {Math.floor((s.speed.reduce((a, b) => a + b, 0) / s.speed.length) * 100) / 100} </Th>
                                    </Tr>
                                ))}
                                 <Tr>
                                        <Th></Th>
                                        <Th>{total.km}</Th>
                                        <Th></Th>
                                        <Th> {moment.duration(total.time, 'seconds').format("HH:mm:ss", { trim: false })} </Th>
                                        <Th>  </Th>
                                    </Tr>
                            </Tbody>
                        </Table>
                    </TableContainer>

                </AlertDialogBody>
                <AlertDialogFooter>
                    <Button ref={cancelRef} onClick={onClose}>
                        OK
                    </Button>
                </AlertDialogFooter>
            </AlertDialogContent>
        </AlertDialog></>
    )
}

export default Statistics