import stravaUserReducer from './reducers/stravaUserReducer'
import stravaActivitiesReducer from './reducers/stravaActivitiesRedcucer'


const store = configureStore({
	reducer: {
		stravauser: stravaUserReducer,
        activities: stravaActivitiesReducer,
	}
})
console.log(store)

export default store